/* Hamberger icon animation */
.box {
  border-radius: 2px;
  position: absolute;
  top: 3rem;
  right: 0;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 40px;
  height: 40px;
  background: transparent;
}

.btn {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 40px;
  cursor: pointer;
}

.span {
  display: block;
  width: 100%;
  box-shadow: 0 2px 10px 0 rgba(0,0,0,0.3);
  border-radius: 3px;
  height: 5px;
  background: #f67280;
  transition: all .3s;
  position: relative;
}

.span + .span {
  margin-top: 4px;
}

.active .span:nth-child(1) {
  -webkit-animation: ease .7s top forwards;
          animation: ease .7s top forwards;
}

.not-active .span:nth-child(1) {
  -webkit-animation: ease .7s top-2 forwards;
          animation: ease .7s top-2 forwards;
}

.active .span:nth-child(2) {
  -webkit-animation: ease .7s scaled forwards;
          animation: ease .7s scaled forwards;
}

.not-active .span:nth-child(2) {
  -webkit-animation: ease .7s scaled-2 forwards;
          animation: ease .7s scaled-2 forwards;
}

.active .span:nth-child(3) {
  -webkit-animation: ease .7s bottom forwards;
          animation: ease .7s bottom forwards;
}

.not-active .span:nth-child(3) {
  -webkit-animation: ease .7s bottom-2 forwards;
          animation: ease .7s bottom-2 forwards;
}

